import { Component, EventEmitter, Input, OnInit, Output, ViewEncapsulation } from '@angular/core';

@Component({
  selector: 'app-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.scss'],
})
export class ButtonComponent implements OnInit {

  @Input() text: string;

  @Input() theme: string;

  @Input('extra-classes') extraClasses: string;

  @Output() buttonClickedEvent = new EventEmitter<Event>();

  themeClasses: any = {
    'primary': 'button--primary'
  }

  themeClass: string = 'button--primary';

  constructor() { }

  ngOnInit(): void {
    this.themeClass = this.themeClasses[this.theme] || this.themeClass;
  }

  onClick(event: Event) {
    this.buttonClickedEvent.emit(event);
  }
}
