import {
  Component,
  OnInit,
  ElementRef,
  ViewChild,
  AfterViewInit,
  ChangeDetectorRef
} from '@angular/core';
import { StoreService } from '../services/store/store.service';
import { TextAnalyzerService } from '../services/text-analyzer-service/text-analyzer.service';

@Component({
  selector: 'app-editable-page',
  templateUrl: './editable-page.component.html',
  styleUrls: ['./editable-page.component.scss']
})
export class EditablePageComponent implements OnInit, AfterViewInit {

  private timer?: any;

  private timeToWaitAfterTypingFinish: number = 1000;

  public isLoading: boolean = false;

  private recommendations: Array<Element> = [];

  public popupVisible: boolean = false;

  public popupXPos: string = '0';

  public popupYPos: string = '0';

  public popupData: any = {word: '', id: null};

  @ViewChild('page') private page: ElementRef;

  constructor(
    private textAnalyzerService: TextAnalyzerService,
    private store: StoreService,
    private changeDetector: ChangeDetectorRef,
  ) { }

  ngOnInit(): void {
    this.store.getTriggerTextAnalysis$().subscribe(async () => {
      this.isLoading = true;
      await this.handleTypingFinished();
      this.isLoading = false;
    });
  }

  ngAfterViewInit(): void {
    this.addListeners();
  }

  private async handleTypingFinished(): Promise<any> {
    this.popupVisible = false;
    const typedText: string = this.page?.nativeElement.innerHTML;
    const analyzedText: string = await this.textAnalyzerService.analyzeText(typedText);
    this.page.nativeElement.innerHTML = analyzedText;
  }

  private addListeners() {
    this.page.nativeElement.addEventListener('click',
      (event: any) => {
        this.listenPageClick(event);
        this.changeDetector.detectChanges();
      });
  }

  private listenPageClick(event: any) {
    this.popupVisible = false;
    if (this.wasRecommendationClicked(event)) {
      this.updateRecommendationInfo(event);
      this.showRecommendation(event);
    }
  }

  private updateRecommendationInfo(event: any) {
    const clickedElement = event.target;
    const id = clickedElement.dataset.recommendationId;
    const word = clickedElement.dataset.word;

    this.popupData = {
      word,
      id
    };
  }

  private wasRecommendationClicked(event: any) {
    const element = event.target;
    return element.classList.contains('word');
  }

  private showRecommendation(event: any) {
    const {x, y} = this.getCoordinatesToMovePopup(event);
    this.popupXPos = `${x}px`;
    this.popupYPos = `${y}px`;
    this.popupVisible = true;
  }

  private getCoordinatesToMovePopup(event: any): any {
    const bounds = this.page.nativeElement.getBoundingClientRect();
    const clickedElement = event.target;
    const clickedElementWidthHalf = clickedElement.offsetWidth / 2;
    const clickedElementHeight = clickedElement.offsetHeight * 2;

    const x = clickedElement.offsetLeft + clickedElementWidthHalf;
    const y = clickedElement.offsetTop + clickedElementHeight;

    return {x, y};
  }
}
