import { Injectable } from '@angular/core';
import { Subject } from 'rxjs';

@Injectable({
    providedIn: 'root',
})
export class StoreService {

    private triggerAnalysis$ = new Subject<boolean>();

    private triggerTextAnalysed$ = new Subject<boolean>();

    private recommendationId: number = 0;

    private recommendations: Array<any> = [];

    private messages: any = {};

    public triggerTextAnalysis() {
        this.triggerAnalysis$.next(true);
    }

    public getTriggerTextAnalysis$() {
        return this.triggerAnalysis$.asObservable();
    }

    public cleanRecommendations() {
        this.recommendationId = 0;
        this.recommendations = [];
        this.messages = {};
    }

    public insertRecommendation(recommendation: any): number {
        this.recommendations[this.recommendationId] = recommendation;
        return this.recommendationId++;
    }

    public insertRecommendationMessage(key: string, message: any): void {
        this.messages[key] = message;
    }

    public getRecommendation(id: number) {
        const recommendation = this.recommendations[id];
        const message = this.messages[recommendation['recommendationMessage']];

        return {...recommendation, message};
    }

    public triggerTextAnalyzed(analyzed: boolean = true) {
        this.triggerTextAnalysed$.next(analyzed);
    }

    public getTriggerTextAnalyzed$() {
        return this.triggerTextAnalysed$.asObservable();
    }
}
